export const BOMB = - 1
export const OFFSETS = [
    [-1, 0], //North
    [1, 0], //South
    [0, 1], //East
    [0, -1], //West
    [-1, -1], //NW
    [-1, 1], //NE
    [1, -1], //SW
    [1, 1] //SE
]

export enum Mask {
    Covered,  
    Flagged,
    uncovered
}

export interface Game {
    masked: Mask[][]
    values: number[][]
    bombs: number
    flagsUsed: number
}

function matrix<T>(row: number, col: number, fill: number, game: Game){
    for(let i = 0; i < game.values.length; i++){
        for(let j = 0; j < game.values.length[0]; j++){
            if(i !== row && col !== j){
                game.values[i][j] = fill
            }
            game.masked[i][j] = Mask.Covered
        }
    }
    game.values[row][col] = 0
}
function fillEmpty(rows: number, cols: number): number[][] {
    let matrix = Array(rows)
    for(let i = 0; i < rows; i++ ){
        matrix[i] = Array(cols).fill(0)
    }
    return matrix
}

export function makeEmptyGame(rows: number, cols: number, bombs: number): Game {
    let values = fillEmpty(rows, cols)
    let masked = fillEmpty(rows, cols)
    let flagsUsed = 0
    return {
        masked,
        values,
        bombs, 
        flagsUsed
    }
}

export function makeGame(row: number, col: number, game: Game): Game {
    matrix(row, col, 0, game)
    addBombs(game, game.bombs, row, col)
    addNumbers(game.values)
    clickCell(game, row, col)
    return game
}
export function revealBombs(game: Game): Game {
    for(let i = 0; i<game.values.length; i++ ){
        for(let j = 0; j < game.values[0].length; j++){
            if(game.values[i][j] === -1){
                game.masked[i][j] = Mask.uncovered
            }
        }
    }
    return game
}
export function checkIfWon(game: Game) {
    let doneGame = false
    for(let i = 0; i<game.masked.length; i++ ){
        for(let j = 0; j < game.masked[0].length; j++){
            console.log(game.masked)
            if(game.masked[i][j] === Mask.Covered || game.masked[i][j] === Mask.Flagged){
                if(game.values[i][j] === -1){
                    doneGame = true
                }
                else{
                    doneGame = false
                    return doneGame
                }
            }
        }
    }
    return doneGame
}

function addBombs(game:Game, numBombs: number, Selectedrow: number, Selectedcol: number) {
    let invalidIndex = false
    let rows = game.values.length
    let cols = game.values[0].length
    for(let i = 0; i < numBombs; i++){
        let row = randomBetween(0,rows)
        let col = randomBetween(0,cols)
        for(let k = 0; k < OFFSETS.length; k++) {
            let [deltaRow, deltaCol] = OFFSETS[k]
            let checkCol = Selectedcol + deltaCol
            let checkRow = Selectedrow + deltaRow
            if((row === checkRow && col === checkCol) || (row === Selectedrow && col === Selectedcol) || game.values[row][col] === -1 ) {
                invalidIndex = true
                break
            }else{
                invalidIndex = false
            }
        }
        if(invalidIndex){
            i--
        }else{
            game.values[row][col] = -1

        }
    }
}

function addNumbers(board: number[][]) {
    let rows = board.length
    let cols = board[0].length
    for(let i = 0; i < rows; i++) {
        for(let j = 0; j < cols; j++) {
            let cell = board[i][j]
            if(cell === BOMB) {
                for(let k = 0; k < OFFSETS.length; k++) {
                    let [deltaRow, deltaCol] = OFFSETS[k]
                    let col = j + deltaCol
                    let row = i + deltaRow
                    if(row >= 0 && row < rows && col >= 0 && col < cols && board[row][col] !== -1) {
                        board[row][col] = board[row][col] + 1
                    }
                }
            }
        }
    }
}
function uncoverBesideEmpty(game: Game) {
    let rows = game.values.length
    let cols = game.values[0].length
    for(let i = 0; i < rows; i++) {
        for(let j = 0; j < cols; j++) {
            let cell = game.values[i][j]
            if(cell === 0 && game.masked[i][j]===Mask.uncovered ) {
                for(let k = 0; k < OFFSETS.length; k++) {
                    let [deltaRow, deltaCol] = OFFSETS[k]
                    let col = j + deltaCol
                    let row = i + deltaRow
                    if(row >= 0 && row < rows && col >= 0 && col < cols ) {
                        game.masked[row][col] = Mask.uncovered
                    }
                }
            }
        }
    }
}


export function clickCell(game: Game, row: number, col: number) {
    let value = game.values[row][col]
    let mask = game.masked[row][col]
    if(mask === Mask.Covered) {
        if(value === BOMB) {
            throw new Error('BOOM')
        } else {
            uncoverEmpty(game, row, col, true)
            uncoverBesideEmpty(game)
            game.masked[row][col] = Mask.uncovered
            return game
        }
    }
}
export function flagged(game: Game, row: number, col: number) {
    let mask = game.masked[row][col]
    if(mask === Mask.uncovered) return game
    if(mask === Mask.Flagged){
        game.masked[row][col] = Mask.Covered
        game.flagsUsed--
        return game
    }
    if(game.flagsUsed >= game.bombs) return game
    game.masked[row][col] = Mask.Flagged
    game.flagsUsed++
    return game

}

function uncoverEmpty(game: Game, row: number, col: number, init: boolean) {
    if((row < 0 || row >= game.values.length) || (col < 0)|| col >= game.values[0].length) return
    let value = game.values[row][col]
    if( game.masked[row][col] === Mask.uncovered) return
   
        if( value !== 0) return
    
    if( game.masked[row][col] === Mask.Flagged) game.flagsUsed--
    game.masked[row][col] = Mask.uncovered
    uncoverEmpty(game, row-1 ,col-1, false)
    uncoverEmpty(game, row-1 ,col, false)
    uncoverEmpty(game, row-1 ,col+1, false)
    uncoverEmpty(game, row ,col-1, false)      
    uncoverEmpty(game, row ,col+1, false)
    uncoverEmpty(game, row+1 ,col-1, false)
    uncoverEmpty(game, row+1 ,col, false)
    uncoverEmpty(game, row+1 ,col+1, false)
}

function randomBetween(leftBound: number, rightBound: number): number {
    return Math.floor(Math.random()*rightBound)+ leftBound
}

