import Vue from 'vue'
import vuetify from './plugins/vuetify'
import App from './App.vue'
import router from './routes/router'

Vue.config.productionTip = false

new Vue({
    router,
    vuetify,
    render: h => h(App),
    computed: {
        isMobile
    }
}).$mount('#app')


function isMobile() {
    const agents = /Android|webOS|iPhone|iPad|iPod|BlackBerry|Windows Phone/i
    return Boolean(navigator.userAgent.match(agents)).valueOf()
}