<template>
    <v-app>
        <v-container>
            <v-content>
                <v-row v-if="game">
                    <v-col cols="12" lg="4" xl="2" >
                        <v-card>
                            <v-card-text class="justify-center text-center overline">minesweeper</v-card-text>
                            <v-card-text class="justify-center text-center overline">Remaining flags: {{game.bombs - game.flagsUsed}}ðŸš©</v-card-text>
                            <v-card-text class="text-center">
                                <div class="text-center black--text white">{{ elapsed }}</div>
                                <v-btn-toggle v-model="difficultyIdx" class="mt-3 pt-3">
                                    <v-btn small text>easy</v-btn>
                                    <v-btn small text>hard</v-btn>
                                </v-btn-toggle>
                            </v-card-text>
                        </v-card>
                    </v-col>
                    <v-col cols="12" lg="8" xl="10">
                        <v-row v-for="(row, i) in values" :key="i" no-gutters class="ma-0 pa-0" style="height: 4vh">
                            <v-col v-for="(col, k) in row" :key="k" class="ma-0 pa-0">
                                <cell v-if="firstClick" :pressed="masked[i][k] === 2" :flagged="masked[i][k] === 1" :value="values[i][k]"
                                @uncover="uncover(i, k)" @flag="flag(i, k)"/>
                                <cell v-else :pressed="false" :flagged="masked[i][k] === 1" :value="values[i][k]" @flag="flag(i, k)" @uncover="firstClicked(i, k)"/>
                            </v-col>
                        </v-row>
                    </v-col>
                    <v-dialog v-model="dialog" persistent max-width="500">
                        <v-card>
                            <v-card-title class="headline">
                                You lost :(
                            </v-card-title>
                            <v-card-text>
                                That's some tough luck right there. Luckily you can try again!
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="green darken-1" text @click="newGame()">
                                    Play again!
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                    <v-dialog v-model="done" persistent max-width="500">
                        <v-card>
                            <v-card-title class="headline">
                                You Won :D
                            </v-card-title>
                            <v-card-text>
                                It took you {{elapsed}} seconds to win
                            </v-card-text>
                            <v-card-text>
                                Wow that was actually amazing, you should try to beat that time :D
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn color="green darken-1" text @click="newGame()">
                                    Play again!
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>
                </v-row>
            </v-content>
        </v-container>
    </v-app>
</template>
<script>
import * as lib from './lib'
import Cell from './Cell.vue'
export default {
    components: {
        Cell
    },
    data() {
        return {
            game: null,
            values: null,
            masked: null,
            firstClick: false,
            difficultyIdx: 0,
            handler: undefined,
            elapsed: 0,
            dialog: false,
            done: false,
            doneTime: 0
        }
    },
    created() {
        this.newGame()
    },
    watch: {
        difficultyIdx() {
            this.newGame()
        }
    },
    methods: {
        newGame() {
            let game
            if(this.difficultyIdx == 0) {
                game = lib.makeEmptyGame(10,8, 10)
            } else {
                game = lib.makeEmptyGame(18, 14, 40)
            }
            this.firstClick = false
            this.game = game
            this.values = game.values
            this.masked = game.masked
            this.dialog = false
            this.done = false
            this.startTimer()
        },
        startTimer() {
            if(this.handler) {
                clearInterval(this.handler)
            }
            let startTime = new Date()
            this.elapsed = 0
            this.handler = setInterval(() => {
                let now = new Date()
                this.elapsed += Math.floor((now.valueOf() - startTime.valueOf()) / 1000)
                startTime = now
            }, 1000)
        },
        uncover(row, col) {
            try{
                let newGame = lib.clickCell(this.game,row, col)
                this.game = newGame

                this.values = this.game.values
                this.masked = this.game.masked
                this.mask = lib.Mask
                this.done = lib.checkIfWon(this.game)
                if(this.done){
                     clearInterval(this.handler)
                    this.doneTime = this.elapsed
                    
                }
                this.$forceUpdate();
            }
            catch(next){
                if(next.message === 'BOOM'){
                    let newGame = lib.revealBombs(this.game)
                    this.game = newGame
                    this.values = this.game.values
                    this.masked = this.game.masked
                    this.mask = lib.Mask
                    this.dialog = true
                    this.$forceUpdate();
                    

                }

            }
        },
        flag(row, col){
            let newGame = lib.flagged(this.game, row, col)
            this.game = newGame
            this.values = this.game.values
            this.masked = this.game.masked
            this.mask = lib.Mask
            this.$forceUpdate();

        },
        async firstClicked(row, col){
            let newGame = await lib.makeGame(row, col, this.game)
            this.game = newGame
            this.values = this.game.values
            this.masked = this.game.masked
            this.mask = lib.Mask
            this.firstClick = true
            this.$forceUpdate();

        }
    }
}
</script>
<style scoped>
* {
  font-family: Impact;
  font-size: 1.3rem;
}

.body {
  background: rgb(255, 237, 179);
}

</style>