<template>
        <button @contextmenu.prevent="flag($event)" :class="classes" :style="styles" @mousedown="mousedown($event)" @mouseup="mouseup($event)">
            <span v-if="flagged">ðŸš©</span>
            <span v-else-if="value === -1 && pressed">ðŸ’£</span>
            <span v-else-if="pressed && value > 0">{{ value }}</span>
            <span v-else>&nbsp;</span>
        </button>
</template>
<script>
export default {
    props: {
        value: Number,
        pressed: Boolean,
        flagged: Boolean
    },
    data() {
        return {
            mousedownTime: 0
        }
    },
    computed: {
        classes() {
            return {
                'cell': !this.pressed,
                'cell-pressed': this.pressed
            }
        },
        styles() {
            return {
                'color': this.color(this.value)
            }
        }
    },
    methods: {
        color(val) {
            switch(val) {
                case 1: return 'blue'
                case 2: return 'green'
                case 3: return 'red'
                case 4: return 'purple'
                case 5: return 'maroon'
                case 6: return 'turquoise'
                case 7: return 'black'
                case 8: return 'grey'
                default: return 'orange'
            }
        },
        flag(event) {
            if(!this.pressed) {
                this.$emit('flag', event)
            }
        },
        mousedown() {
            this.mousedownTime = Date.now()
        },
        mouseup(event) {
            if(event.which === 1){
                let now = Date.now()
                if((now - this.mousedownTime) > 1000) {
                    this.$emit('flag', event)
                } else if(!this.pressed && !this.flagged) {
                    this.$emit('uncover', event)
                }
            }
        }
    }
}
</script>
<style scoped>
.cell {
    width: 100%;
    height: 100%;
    text-align: center;
    vertical-align: middle;
    cursor: default;
    padding: 1px;
    margin: 0px;
    border-left: 2px solid white;
    border-top: 2px solid white;
    border-right: 2px solid #808080;
    border-bottom: 2px solid #808080;
    background-color: #c0c0c0;
    font-size: 15px;
}

.cell-pressed {
    width: 100%;
    height: 100%;
    text-align: center;
    vertical-align: middle;
    cursor: default;
    padding-left: 2px;
    padding-top: 2px;
    padding-bottom: 0px;
    padding-right: 0px;
    margin: 0px;
    border-left: 2px solid #808080;
    border-top: 2px solid #808080;
    border-right: 2px solid #c0c0c0;
    border-bottom: 2px solid #c0c0c0;
    background-color: #c0c0c0;
    font-size: 15px;
}
</style>